/* app.css */

/* Default to light theme */
:root {
  --background-color: white;
  --text-color: #7469B6;
}

/* Dark theme */
[data-theme='dark'] {
  --background-color: #121212;
  --text-color: #FFFED3;
}

html {
  background: var(--background-color);
  color: var(--text-color);
}

body {
  background: var(--background-color);
  color: var(--text-color);
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}